@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;600&display=swap");
:root {
  --h1-fs:1.75rem;
  --par-fs:0.9375rem;
  --btn-fs:0.9375rem;
  --num-fs:1rem;
}

html ::selection {
  background-color: #1683ff;
  color: #ffffff;
}

body {
  background-color: #fafafb;
  font-family: "Source Sans Pro", sans-serif;
}

.container.text-center {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2rem;
}

h1 {
  font-size: 2.375rem;
  font-weight: 200;
  opacity: 0.8;
}

h2 {
  font-size: 2.375rem;
  font-weight: 600;
}

h3 {
  font-weight: 600;
  font-size: 1.5rem;
}

p {
  opacity: 0.8;
  max-width: 470px;
}

.li {
  margin: 0 auto;
  max-width: 350px;
  text-align: left;
  padding: 2rem;
  background-color: #fff;
  margin-bottom: 2rem;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.216);
  border-radius: 5px;
}

.druhej {
  max-width: 350px;
  margin: 0 auto;
}

.list .list--first {
  border-top: 5px solid rgb(0, 255, 238);
}
.list .list--second {
  border-top: 5px solid red;
}
.list .list--third {
  border-top: 5px solid orange;
}
.list .list--fourth {
  border-top: 5px solid rgb(0, 110, 255);
}

@media (min-width: 992px) {
  .list {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
  }
  .list > * {
    min-width: 300px;
  }
}